o de grifos.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Muestras compuestas","subtitulo":""}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Aquellas que son usadas, en su mayoría, para vertimientos domésticos e industriales. Debido a la variación horaria de su caudal son muy utilizadas en el monitoreo de ríos, vertimientos o procesos industriales en línea. Su toma se realiza en un mismo punto en intervalos de tiempo previamente definidos y el volumen de cada muestra parcial puede ser constante o variar en función del caudal.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Cuando hay variación del caudal","subtitulo":""}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-4"},[_vm._v("Para los casos en que se requiere variar el volumen en cada toma, se recolectan muestras parciales (alícuotas). Ejemplo: cada 2 o 3 horas, cuyo "),_c('strong',[_vm._v("volumen")]),_vm._v(" se obtiene según la siguiente relación:")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-5 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"270px"},attrs:{"src":require("@/assets/curso/tema1/img18.svg"),"alt":"Imagen que representa la fórmula Vp = Qp * (Vc / N * Q)"}})])]),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"bg-c13 my-2 my-lg-0"},[_c('div',{staticClass:"p-3"},[_c('p',{staticClass:"mb-1 text-small"},[_c('strong',[_vm._v("V"),_c('sub',[_vm._v("p")])]),_vm._v(": Volumen de cada muestra parcial.")]),_c('p',{staticClass:"mb-1 text-small"},[_c('strong',[_vm._v("V"),_c('sub',[_vm._v("c")])]),_vm._v(": Volumen de muestra compuesta.")]),_c('p',{staticClass:"mb-1 text-small"},[_c('strong',[_vm._v("Q"),_c('sub',[_vm._v("p")])]),_vm._v(": Caudal parcial del agua.")]),_c('p',{staticClass:"mb-1 text-small"},[_c('strong',[_vm._v("Q")]),_vm._v(": Caudal promedio.")]),_c('p',{staticClass:"mb-1 text-small"},[_c('strong',[_vm._v("N")]),_vm._v(": Número de muestras parciales.")])])])