ipales técnicas de muestreo, se pueden mencionar y explicar las siguientes:")]),_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--verde"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Muestreo manual"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("El muestreo manual se realiza cuando se tienen sitios de fácil acceso o aquellos que por medio de ciertas adaptaciones puedan facilitar la toma de muestras. La ventaja de este tipo de muestreo es permitir al encargado de tomar la muestra, observar los cambios en las características del agua en cuanto a sustancias flotantes, color, olor, aumento o disminución de caudales, entre otros; sin embargo, este solo es aceptable para los criterios de control y vigilancia, si la muestra es representativa de la calidad del agua del sitio de muestreo particular, motivo por el cual se requiere establecer que la información obtenida de estas muestras puntuales tomadas en un sitio y tiempo dados es única para ese lugar y tiempo seleccionado.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Muestreo automático"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Para aquellos lugares de difícil acceso se recomiendan los muestreadores automáticos, ya que cuentan con más precisión en la toma de la muestra, aun cuando su mayor desventaja sea el montaje y calibración del equipo, pues su aplicación implica la instalación de equipos (antenas, paneles solares, etc.) y herramientas (licencias de trasmisión, software) que elevan el costo, convirtiéndose en un factor limitante para la implementación de este tipo de muestreo. Entre los equipos automáticos más representativos para determinar la calidad del agua destinada para consumo humano, se destacan los: muestreadores secuenciales automáticos para proveer muestras compuestas en un período extenso de tiempo, Sistemas de monitoreo en línea (sistema SAMOS para detección de pesticidas, y nuevos sensores), Sistemas biológicos de alarma temprana que alerten si hay contaminación en la muestra y muestreadores pasivos, así llamados porque simulan captación biológica.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Muestreo mixto"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Se pueden poner en marcha programas de muestreo que involucren la utilización de los dos tipos de muestreo mencionados anteriormente (muestreo mixto), convirtiendo el monitoreo en un sistema integrado que permite la verificación manual de los resultados obtenidos de forma automática. Dicha verificación es realizada aleatoriamente, de tal manera que se pueda realizar la calibración, ajuste y mantenimiento de los equipos automáticos. Las técnicas del muestreo de agua para análisis microbiológicos básicamente incluyen procedimientos para prevenir su contaminación durante el llenado de los recipientes por microorganismos presentes en el exterior de los grifos, accesorios, en el ambiente o en el operario. Las técnicas de llenado de los recipientes se pueden dividir en dos: muestreo en el grifo o muestreo por inmersión.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Muestreo por inmersión"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("El muestreo por inmersión se realiza cuando se requiere tomar muestras de profundidad, por ejemplo en el caso de muestreos de tanques de almacenamiento que no cuenten con dispositivos de muestras o en el agua de manantiales o pozos, piscinas. En estos casos la muestra se debe tomar con un recipiente estéril en su interior y exterior. Tan pronto se va a tomar la muestra, se debe retirar la envoltura protectora del recipiente y con la ayuda de una vara de muestreo se lleva a cabo la toma de muestra. En el caso de toma de las piscinas, la muestra se debe tomar de -10 a -20 cm de profundidad y el recipiente se introduce horizontalmente para evitar la pérdida de tiosulfato. Para balnearios se hace de manera similar haciendo la toma a -20 a -30. En el caso de pozos o manantiales sin dispositivos de muestreo, la toma se debe hacer con bombas o dispositivos sumergibles estériles.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Muestreo tomado al azar"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Se basa en una muestra que se toma totalmente al azar, por medio de un recipiente abierto, el cual se coloca en un solo punto en la superficie (arroyo, embalse, rio, etc.); estas muestras por lo general se toman antes de hacer otro trabajo en el lugar para no afectar la corriente y la posterior recolección; para este ejercicio se utilizan muestreadores colectores que suelen pararse corriente abajo de la botella durante la operación de llenado.")])])])]),_vm._m(45),_vm._m(46),_vm._m(47),_vm._m(48),_c('p',[_vm._v("Las muestras de agua pueden provenir de fuentes superficiales (ríos, arroyos, canales, represas, lagos) o subterráneas (pozos) y este aspecto definirá las condiciones de muestreo.")]),_c('p',{staticClass:"mb-5"},[_vm._v("En función de la fuente que se vaya a muestrear, y para asegurar que la muestra sea lo más representativa posible del total, se tendrán en cuenta las siguientes consideraciones:")]),_c('div',{staticClass:"tarjeta tarjeta--azul p-4 mb-5"},[_c('SlyderA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"row m-1"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Agua de red")]),_c('p',[_vm._v("Se abrirá el grifo o canilla y se dejará que el agua corra por lo menos durante 5 minutos de manera de tener purgada toda la cañería que llega desde el tanque. El ramal donde se encuentre el grifo debe ser el principal, proveniente de la red, y no debe estar conectado en el trayecto con otras cañerías, filtros, ablandadores u otros artefactos que puedan alterar la calidad del agua del ramal principal. También se debe tomar la precaución de retirar del grifo o boca de salida las mangueras u otros accesorios, y de limpiarlo tratando de eliminar s